import { css } from "@emotion/css";
import styled from "@emotion/styled";

import {
  HvVerticalNavigation,
  theme,
  verticalNavigationTreeClasses
} from "@hitachivantara/uikit-react-core";

interface StyledHvVerticalNavigationProps {
  isCompactMode: boolean;
}

const StyledHvVerticalNavigation = styled(
  HvVerticalNavigation
)<StyledHvVerticalNavigationProps>`
  height: calc(100vh - ${theme.header.height});
  top: ${theme.header.height};
  z-index: ${theme.zIndices.overlay};

  position: ${props => {
    if (props.isCompactMode) {
      return "fixed";
    }

    return "sticky";
  }};
`;

const navigationPopupStyles = css({
  maxHeight: `calc(100vh - ${theme.header.height})`,
  overflowY: "auto",
  boxShadow: theme.colors.shadow,
  [`& .${verticalNavigationTreeClasses.popup}`]: { boxShadow: "none" }
});

export { StyledHvVerticalNavigation, navigationPopupStyles };
